<template>
  <el-row>
    <el-col :span="20" :offset="2">
      <el-form :model="projectForm" :rules="rules" ref="projectForm" label-position="top" class="demo-ruleForm">
        <h4>产品基本信息</h4>
        <el-form-item label="项目名称"	 prop="projectName">
          <el-input v-model="projectForm.projectName"></el-input>
        </el-form-item>
        <el-form-item label="投资期限" prop="investPeriod">
          <el-input v-model="projectForm.investPeriod"></el-input>
        </el-form-item>

        <el-form-item label="发行人" v-if="isEdit" prop="publisher">
          <el-input v-model="projectForm.publisher"></el-input>
        </el-form-item>
        <el-form-item label="担保人" v-if="isEdit" prop="guarantor">
          <el-input v-model="projectForm.guarantor"></el-input>
        </el-form-item>
        <el-form-item label="备案机构"	v-if="isEdit" prop="filingAgency">
          <el-input v-model="projectForm.filingAgency"></el-input>
        </el-form-item>
        <el-form-item label="承销商" v-if="isEdit" prop="underwriter">
          <el-input v-model="projectForm.underwriter"></el-input>
        </el-form-item>
        <el-form-item label="预募集资金规模"	v-if="isEdit" prop="preRaisedFunds">
          <el-input v-model="projectForm.preRaisedFunds"></el-input>
        </el-form-item>
        <el-form-item label="投资者类型" v-if="isEdit" prop="investorType">
          <el-input v-model="projectForm.investorType"></el-input>
        </el-form-item>
        <el-form-item label="认购起点金额"	v-if="isEdit" prop="startAmount">
          <el-input v-model="projectForm.startAmount"></el-input>
        </el-form-item>
        <el-form-item label="销售方式" v-if="isEdit" prop="salesMethod">
          <el-input v-model="projectForm.salesMethod"></el-input>
        </el-form-item>
        <el-form-item label="还款来源"	v-if="isEdit" prop="sourceOfRepayment">
          <el-input type="textarea" v-model="projectForm.sourceOfRepayment"></el-input>
        </el-form-item>
        <el-form-item label="投资标的" v-if="isEdit" prop="investTarget">
          <el-input v-model="projectForm.investTarget"></el-input>
        </el-form-item>
        <el-form-item label="认购方式"	v-if="isEdit" prop="subscriptionMethod">
          <el-input type="textarea" v-model="projectForm.subscriptionMethod"></el-input>
        </el-form-item>
        <el-form-item label="咨询认购地点" v-if="isEdit" prop="consultPlace">
          <el-input v-model="projectForm.consultPlace"></el-input>
        </el-form-item>

        <h4 v-if="isEdit">认购日期和收益起始计算</h4>
        <el-form-item label="认购期间"	v-if="isEdit" prop="subscriptionPeriod">
          <el-input v-model="projectForm.subscriptionPeriod"></el-input>
        </el-form-item>
        <el-form-item label="产品成立日" v-if="isEdit" prop="establishDate">
          <el-input v-model="projectForm.establishDate"></el-input>
        </el-form-item>
        <el-form-item label="产品起息日"	v-if="isEdit" prop="valueDate">
          <el-input v-model="projectForm.valueDate"></el-input>
        </el-form-item>
        <el-form-item label="投资者资金到帐日" v-if="isEdit" prop="investorFundsArrivalDate">
          <el-input v-model="projectForm.investorFundsArrivalDate"></el-input>
        </el-form-item>

        <el-form-item
          v-for="(range, index) in projectForm.rateRangeList"
          :label="'利息区间 [' + index + ']'"
          :key="index"
          :prop="'rateRangeList.' + index + '.rate'"
          :rules="{
            required: true, message: '利息区间信息不能为空', trigger: 'blur'
          }">
          <el-row :gutter="30">
            <el-col :span="8">
              <el-form-item>
                <el-input placeholder="起始金额" v-model.number="range.startMoney"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item >
                <el-input placeholder="结束金额" v-model.number="range.endMoney"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item >
                <el-input placeholder="区间利率" v-model="range.rate"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="2">
              <el-button type="danger" @click.prevent="removeRange(range)" icon="el-icon-delete" size="small"></el-button>
            </el-col>
          </el-row>
        </el-form-item>
        <br>
        <el-form-item>
          <el-row type="flex" class="row-bg">
            <el-col :span="4">
              <el-button type="info" @click="addRange" size="medium" icon="el-icon-plus">新增利息区间</el-button>
            </el-col>
            <el-col :span="4" :push="14">
              <el-button type="primary" :loading="saveBTLoading" @click="submitForm('projectForm')" size="medium" icon="el-icon-success">保存</el-button>
            </el-col>
            <el-col :span="4" :push="13">
              <el-button type="warning" @click="resetForm('projectForm')" size="medium"  icon="el-icon-refresh">重置</el-button>
            </el-col>
          </el-row>
        </el-form-item>

        <el-tooltip placement="top" content="回到顶部">
          <back-to-top :custom-style="myBackToTopStyle" :visibility-height="300" :back-position="50" transition-name="fade"/>
        </el-tooltip>
      </el-form>
    </el-col>
  </el-row>
</template>

<script>
  import { getProjectDetailById, updateProject, deleteRateRange, addProject} from '@/api/project'
  import BackToTop from '@/components/BackToTop'

  const defaultForm = {
    projectId: '',
    projectName: '', // 项目名称
    investPeriod: '', // 投资期限
    publisher:'', //发行人
    guarantor:'', //担保人
    filingAgency:'', //备案机构
    underwriter:'', //承销商
    preRaisedFunds:'', //预募集资金规模
    investorType:'', //投资者类型
    startAmount:'', //认购起点金额
    salesMethod:'', //销售方式
    sourceOfRepayment:'', //还款来源
    investTarget:'', //投资标的
    subscriptionMethod:'', //认购方式
    consultPlace:'', //咨询认购地点
    subscriptionPeriod:'周一至周六，上午9点-12点；下午2点-7点', //认购期间
    establishDate:'产品认购完成第一个工作日，具体以产品实际募集情况，不超过3个工作日', //产品成立日
    valueDate:'产品成立日【当日】，具体日期以实际情况为准（融资主体出具成立通知书）', //产品起息日
    investorFundsArrivalDate:'投资者投资到期后，本金及当期收益将于合同规定日期后的3个工作日内到账，到期日及投资者资金到帐日之间，不计利息。', //投资者资金到账日
    rateRangeList: [{
      projectId:'',
      rateRangeId:'',
      startMoney: '',
      endMoney: '',
      rate: ''
    }]
  };

  export default {
    name: 'ProjectDetail',
    components: { BackToTop },
    props: {
      isEdit: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        projectId: '',
        projectForm: Object.assign({}, defaultForm),
        saveBTLoading: false,
        userListOptions: [],
        rules: {
          projectName: [
            { required: true, message: '请填写项目名称', trigger: 'blur' }
          ],
          investPeriod: [
            { required: true, message: '请填写投资期限', trigger: 'blur' }
          ],
        },
        myBackToTopStyle: {
          right: '50px',
          bottom: '50px',
          width: '40px',
          height: '40px',
          'border-radius': '4px',
          'line-height': '45px', // 请保持与高度一致以垂直居中 Please keep consistent with height to center vertically
          background: '#e7eaf1'// 按钮的背景颜色 The background color of the button
        }
      }
    },
    computed: {
    },
    created() {
      if (this.hasPermission('project:update')) {
        if (this.isEdit) {
          this.projectId = this.$route.params && this.$route.params.id
          this.getProjectDetail(this.projectId)
        } else {
          this.projectForm = Object.assign({}, defaultForm);
        }
      }
    },
    methods: {
      getProjectDetail(projectId) {
        getProjectDetailById(projectId).then(response => {
          this.projectForm = response.data
        }).catch(err => {
          console.error(err)
        })
      },
      addProject(form){
        this.saveBTLoading = true
        addProject(form).then(response => {
          if (response.returnCode === 200){
            this.saveBTLoading = false;
            this.$confirm('是否前往完善项目信息', '新增项目成功', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'info'
            }).then(() => {
              this.$router.push({path: '/project/edit/'+ response.data})
            }).catch(() => {
              this.$message.success('新增项目成功！');
              this.$router.push({path: '/project/index'})
            });
          } else {
            this.saveBTLoading = false;
            this.$message.error(response.message);
            console.error(response)
          }
        }).catch(error => {
          this.saveBTLoading = false;
          console.error(error);
          this.$message.error(error.message)
        })
      },
      submitForm() {
        this.$refs.projectForm.validate(valid => {
          if (valid) {
            if(this.isEdit) {
              this.update(this.projectForm);
            }else{
              this.addProject(this.projectForm);
            }
          } else {
            console.error('error submit!!');
            return false;
          }
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      update(form) {
        this.saveBTLoading = true;
        updateProject(form).then(response => {
          if (response.returnCode === 200){
            this.$message.success('更新项目信息成功！');
            this.saveBTLoading = false;
            this.$router.push({path: '/project/index'})
          } else {
            this.saveBTLoading = false;
            console.error(response);
            this.$message.error(response.message);
          }
        }).catch(error => {
          this.saveBTLoading = false;
          this.$message.error("更新项目信息失败");
          console.error(error);
        })
      },
      addRange() {
        this.projectForm.rateRangeList.push({
          startMoney: '',
          endMoney: '',
          projectId:this.projectId,
          rateRangeId: '',
          rate:''
        });
      },
      removeRange(item) {
        if(!this.isEdit){
          let index = this.projectForm.rateRangeList.indexOf(item);
          if (index !== -1) {
            this.projectForm.rateRangeList.splice(index, 1)
          }
        }else{
          this.$confirm('确定要删除该利息区间吗？', 'Warning', {
            confirmButtonText: '删除',
            showCancelButton: true,
            type: 'warning'
          }).then(() => {
            deleteRateRange(item.rateRangeId).then(response => {
              if (response.returnCode === 200){
                this.$message.success('删除成功！');
                this.getProjectDetail(this.projectId)
              } else {
                console.error(response);
                this.$message.error(response.message)
              }
            }).catch(error => {
              console.error(error);
              this.$message.error(error.message);
            })
          });
        }
      },
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>

